"use client";

import { motion, useInView } from "framer-motion";
import { useRef, useState } from "react";
import {
  Circle,
  Play,
  Share2,
  Clock,
  MousePointerClick,
  Check,
  ArrowRight,
} from "lucide-react";
import { cn } from "@/lib/utils";

const steps = [
  {
    number: "01",
    icon: Circle,
    title: "Record",
    description:
      "Click Start Recording and perform any actions on a webpage. We capture every click, keystroke, and scroll.",
    color: "from-blue-500 to-cyan-500",
    details: [
      "Click Start Recording button",
      "Navigate to any webpage",
      "Perform your actions naturally",
      "Click Stop when done",
    ],
  },
  {
    number: "02",
    icon: Play,
    title: "Replay",
    description:
      "Run your saved automations with a single click. Watch them execute with visual feedback.",
    color: "from-purple-500 to-pink-500",
    details: [
      "Open extension popup",
      "Find your automation",
      "Click the play button",
      "Watch it run perfectly",
    ],
  },
  {
    number: "03",
    icon: Share2,
    title: "Share",
    description:
      "Generate a shareable link instantly. Anyone can run your automation without installing anything.",
    color: "from-orange-500 to-red-500",
    details: [
      "Click the share icon",
      "Link copied to clipboard",
      "Send to anyone",
      "They run it instantly",
    ],
  },
  {
    number: "04",
    icon: Clock,
    title: "Schedule",
    description:
      "Set automations to run on a schedule. Perfect for recurring tasks like reports or backups.",
    color: "from-green-500 to-emerald-500",
    details: [
      "Open automation settings",
      "Enable scheduling",
      "Choose frequency",
      "Save and forget",
    ],
  },
];

function StepCard({
  step,
  index,
  isActive,
  onClick,
}: {
  step: (typeof steps)[0];
  index: number;
  isActive: boolean;
  onClick: () => void;
}) {
  const ref = useRef<HTMLDivElement>(null);
  const isInView = useInView(ref, { once: true, margin: "-50px" });

  return (
    <motion.div
      ref={ref}
      initial={{ opacity: 0, x: -20 }}
      animate={isInView ? { opacity: 1, x: 0 } : {}}
      transition={{ duration: 0.5, delay: index * 0.15 }}
      onClick={onClick}
      className={cn(
        "relative cursor-pointer group",
        "p-6 rounded-2xl border transition-all duration-300",
        isActive
          ? "bg-card border-primary/30 shadow-soft-md"
          : "bg-card/30 border-border/30 hover:bg-card/50 hover:border-border/50"
      )}
    >
      {/* Connection line */}
      {index < steps.length - 1 && (
        <div className="hidden lg:block absolute top-1/2 -right-3 w-6 border-t-2 border-dashed border-border/30" />
      )}

      <div className="flex items-start gap-4">
        {/* Step number & icon */}
        <div className="relative">
          <div
            className={cn(
              "w-14 h-14 rounded-2xl flex items-center justify-center transition-all duration-300",
              isActive
                ? `bg-gradient-to-br ${step.color} shadow-soft-md`
                : "bg-secondary/50 group-hover:bg-secondary"
            )}
          >
            <step.icon
              className={cn(
                "w-6 h-6 transition-colors",
                isActive ? "text-white" : "text-muted-foreground"
              )}
            />
          </div>
          <span
            className={cn(
              "absolute -top-2 -right-2 text-xs font-bold px-2 py-0.5 rounded-full transition-colors",
              isActive
                ? "bg-primary text-primary-foreground"
                : "bg-muted text-muted-foreground"
            )}
          >
            {step.number}
          </span>
        </div>

        {/* Content */}
        <div className="flex-1 min-w-0">
          <h3
            className={cn(
              "text-lg font-semibold mb-1 transition-colors",
              isActive && "text-primary"
            )}
          >
            {step.title}
          </h3>
          <p className="text-sm text-muted-foreground line-clamp-2">
            {step.description}
          </p>
        </div>

        <ArrowRight
          className={cn(
            "w-5 h-5 shrink-0 transition-all",
            isActive
              ? "text-primary translate-x-1"
              : "text-muted-foreground/50 group-hover:text-muted-foreground"
          )}
        />
      </div>
    </motion.div>
  );
}

function StepDetail({ step }: { step: (typeof steps)[0] }) {
  return (
    <motion.div
      key={step.number}
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      exit={{ opacity: 0, y: -20 }}
      transition={{ duration: 0.3 }}
      className="relative"
    >
      {/* Visual mockup */}
      <div className="rounded-2xl border border-border/50 bg-card/80 backdrop-blur-xl p-8 shadow-soft-lg">
        <div className="flex items-center gap-4 mb-6">
          <div
            className={cn(
              "w-16 h-16 rounded-2xl flex items-center justify-center bg-gradient-to-br shadow-soft-md",
              step.color
            )}
          >
            <step.icon className="w-8 h-8 text-white" />
          </div>
          <div>
            <h4 className="text-xl font-semibold">{step.title}</h4>
            <p className="text-muted-foreground">{step.description}</p>
          </div>
        </div>

        {/* Step details */}
        <div className="space-y-3">
          {step.details.map((detail, index) => (
            <motion.div
              key={index}
              initial={{ opacity: 0, x: -10 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: index * 0.1 }}
              className="flex items-center gap-3 p-3 rounded-xl bg-secondary/50 border border-border/30"
            >
              <div
                className={cn(
                  "w-6 h-6 rounded-full flex items-center justify-center text-white text-xs font-semibold bg-gradient-to-br",
                  step.color
                )}
              >
                {index + 1}
              </div>
              <span className="text-sm font-medium">{detail}</span>
              <Check className="w-4 h-4 text-success ml-auto" />
            </motion.div>
          ))}
        </div>

        {/* Decorative gradient */}
        <div
          className={cn(
            "absolute -inset-0.5 rounded-2xl bg-gradient-to-br opacity-10 -z-10 blur-sm",
            step.color
          )}
        />
      </div>
    </motion.div>
  );
}

export function HowItWorks() {
  const [activeStep, setActiveStep] = useState(0);
  const headerRef = useRef<HTMLDivElement>(null);
  const isHeaderInView = useInView(headerRef, { once: true, margin: "-100px" });

  return (
    <section
      id="how-it-works"
      className="section-padding relative overflow-hidden"
    >
      {/* Background */}
      <div className="absolute inset-0 bg-gradient-to-b from-background via-secondary/20 to-background" />

      <div className="container-custom relative z-10">
        {/* Header */}
        <motion.div
          ref={headerRef}
          initial={{ opacity: 0, y: 20 }}
          animate={isHeaderInView ? { opacity: 1, y: 0 } : {}}
          transition={{ duration: 0.6 }}
          className="text-center max-w-3xl mx-auto mb-16"
        >
          <motion.div
            initial={{ opacity: 0, scale: 0.9 }}
            animate={isHeaderInView ? { opacity: 1, scale: 1 } : {}}
            transition={{ duration: 0.5, delay: 0.1 }}
            className="inline-flex items-center gap-2 px-4 py-2 rounded-full bg-secondary/80 border border-border/50 mb-6"
          >
            <MousePointerClick className="w-4 h-4 text-primary" />
            <span className="text-sm font-medium">How It Works</span>
          </motion.div>

          <h2 className="text-display-md md:text-display-lg font-bold tracking-tight mb-4">
            Four steps to
            <br />
            <span className="text-gradient-primary">automation bliss</span>
          </h2>

          <p className="text-lg text-muted-foreground">
            No code required. No complex setup. Just record, replay, and let
            automation handle the rest.
          </p>
        </motion.div>

        {/* Steps Grid */}
        <div className="grid lg:grid-cols-2 gap-8 lg:gap-12 items-start">
          {/* Steps list */}
          <div className="space-y-4">
            {steps.map((step, index) => (
              <StepCard
                key={step.number}
                step={step}
                index={index}
                isActive={activeStep === index}
                onClick={() => setActiveStep(index)}
              />
            ))}
          </div>

          {/* Step detail */}
          <div className="lg:sticky lg:top-32">
            <StepDetail step={steps[activeStep]} />
          </div>
        </div>
      </div>
    </section>
  );
}
