"use client";

import { useState, useEffect } from "react";
import { motion, AnimatePresence } from "framer-motion";
import {
  Play,
  Square,
  Zap,
  MoreHorizontal,
  Share2,
  Clock,
  Check,
  Circle,
  Loader2,
} from "lucide-react";
import Image from "next/image";
import { cn } from "@/lib/utils";

const mockAutomations = [
  {
    id: "1",
    name: "Fill login form",
    events: 12,
    duration: "8s",
    time: "2 min ago",
  },
  {
    id: "2",
    name: "Submit weekly report",
    events: 24,
    duration: "15s",
    time: "1 hour ago",
  },
  {
    id: "3",
    name: "Export analytics data",
    events: 8,
    duration: "5s",
    time: "3 hours ago",
  },
];

const recordingSteps = [
  "Click on email input",
  "Type: user@example.com",
  "Click on password input",
  "Type: ••••••••",
  "Click Sign In button",
];

export function DemoPanel() {
  const [isRecording, setIsRecording] = useState(false);
  const [currentStep, setCurrentStep] = useState(0);
  const [eventCount, setEventCount] = useState(0);
  const [runningId, setRunningId] = useState<string | null>(null);
  const [showShareSuccess, setShowShareSuccess] = useState<string | null>(null);

  // Recording simulation
  useEffect(() => {
    if (isRecording) {
      const interval = setInterval(() => {
        setEventCount((prev) => {
          if (prev >= recordingSteps.length - 1) {
            setIsRecording(false);
            return prev;
          }
          setCurrentStep(prev + 1);
          return prev + 1;
        });
      }, 1500);
      return () => clearInterval(interval);
    }
  }, [isRecording]);

  const startRecording = () => {
    setIsRecording(true);
    setEventCount(0);
    setCurrentStep(0);
  };

  const stopRecording = () => {
    setIsRecording(false);
  };

  const runAutomation = (id: string) => {
    setRunningId(id);
    setTimeout(() => setRunningId(null), 3000);
  };

  const shareAutomation = (id: string) => {
    setShowShareSuccess(id);
    setTimeout(() => setShowShareSuccess(null), 2000);
  };

  return (
    <div className="relative">
      {/* Browser chrome mockup */}
      <div className="rounded-2xl border border-border/50 bg-card/80 backdrop-blur-xl shadow-soft-xl overflow-hidden">
        {/* Browser header */}
        <div className="flex items-center gap-2 px-4 py-3 border-b border-border/50 bg-secondary/30">
          <div className="flex gap-1.5">
            <div className="w-3 h-3 rounded-full bg-red-400/80" />
            <div className="w-3 h-3 rounded-full bg-yellow-400/80" />
            <div className="w-3 h-3 rounded-full bg-green-400/80" />
          </div>
          <div className="flex-1 flex justify-center">
            <div className="px-4 py-1 rounded-md bg-secondary/50 text-xs text-muted-foreground font-mono">
              example.com/login
            </div>
          </div>
          <div className="w-16" />
        </div>

        <div className="flex">
          {/* Left side - Website mockup */}
          <div className="flex-1 p-8 bg-gradient-to-br from-slate-50 to-slate-100 dark:from-slate-900 dark:to-slate-800 min-h-[400px]">
            <div className="max-w-sm mx-auto">
              {/* Login form mockup */}
              <div className="space-y-6">
                <div className="text-center mb-8">
                  <div className="w-12 h-12 rounded-xl bg-primary/10 flex items-center justify-center mx-auto mb-4">
                    <Zap className="w-6 h-6 text-primary" />
                  </div>
                  <h3 className="text-lg font-semibold">Welcome back</h3>
                  <p className="text-sm text-muted-foreground">
                    Sign in to your account
                  </p>
                </div>

                <div className="space-y-4">
                  <div className="space-y-2">
                    <label className="text-sm font-medium">Email</label>
                    <motion.div
                      className={cn(
                        "px-4 py-3 rounded-xl border bg-background text-sm",
                        isRecording && currentStep === 1
                          ? "border-primary ring-2 ring-primary/20"
                          : "border-border"
                      )}
                    >
                      <AnimatePresence mode="wait">
                        {isRecording && currentStep >= 1 ? (
                          <motion.span
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            className="text-foreground"
                          >
                            user@example.com
                          </motion.span>
                        ) : (
                          <span className="text-muted-foreground">
                            Enter your email
                          </span>
                        )}
                      </AnimatePresence>
                    </motion.div>
                  </div>

                  <div className="space-y-2">
                    <label className="text-sm font-medium">Password</label>
                    <motion.div
                      className={cn(
                        "px-4 py-3 rounded-xl border bg-background text-sm",
                        isRecording && currentStep === 3
                          ? "border-primary ring-2 ring-primary/20"
                          : "border-border"
                      )}
                    >
                      <AnimatePresence mode="wait">
                        {isRecording && currentStep >= 3 ? (
                          <motion.span
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            className="text-foreground"
                          >
                            ••••••••
                          </motion.span>
                        ) : (
                          <span className="text-muted-foreground">
                            Enter your password
                          </span>
                        )}
                      </AnimatePresence>
                    </motion.div>
                  </div>

                  <motion.button
                    className={cn(
                      "w-full py-3 rounded-xl font-medium text-sm transition-all",
                      isRecording && currentStep === 4
                        ? "bg-primary text-primary-foreground ring-2 ring-primary/30"
                        : "bg-primary/90 text-primary-foreground hover:bg-primary"
                    )}
                  >
                    Sign In
                  </motion.button>
                </div>
              </div>
            </div>

            {/* Cursor animation */}
            <AnimatePresence>
              {isRecording && (
                <motion.div
                  initial={{ opacity: 0, scale: 0 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 0 }}
                  className="absolute pointer-events-none"
                  style={{
                    left:
                      currentStep === 0 || currentStep === 1
                        ? "35%"
                        : currentStep === 2 || currentStep === 3
                        ? "35%"
                        : "50%",
                    top:
                      currentStep === 0 || currentStep === 1
                        ? "45%"
                        : currentStep === 2 || currentStep === 3
                        ? "58%"
                        : "72%",
                  }}
                >
                  <motion.div
                    animate={{
                      x: [0, 5, 0],
                      y: [0, 5, 0],
                    }}
                    transition={{
                      duration: 0.5,
                      repeat: Infinity,
                    }}
                    className="w-6 h-6 relative"
                  >
                    <svg
                      viewBox="0 0 24 24"
                      fill="none"
                      className="w-full h-full drop-shadow-lg"
                    >
                      <path
                        d="M5.5 3.21V20.8c0 .45.54.67.85.35l4.86-4.86a.5.5 0 0 1 .35-.15h6.87c.48 0 .73-.58.39-.92L6.35 2.76a.5.5 0 0 0-.85.45Z"
                        fill="white"
                        stroke="black"
                        strokeWidth="1.5"
                      />
                    </svg>
                  </motion.div>
                </motion.div>
              )}
            </AnimatePresence>
          </div>

          {/* Right side - Extension panel */}
          <div className="w-80 border-l border-border/50 bg-card/60 backdrop-blur">
            {/* Extension header */}
            <div className="flex items-center justify-between px-4 py-3 border-b border-border/50">
              <div className="flex items-center gap-2">
                <div className="w-8 h-8 rounded-lg bg-gradient-to-br from-slate-700 to-slate-800 p-0.5 shadow-sm">
                  <Image
                    src="/simplest.png"
                    alt="Simplest"
                    width={32}
                    height={32}
                    className="w-full h-full object-contain rounded-md"
                  />
                </div>
                <div>
                  <h4 className="text-sm font-semibold logo-gradient-text">
                    Simplest
                  </h4>
                  <p className="text-[10px] text-muted-foreground">Automation</p>
                </div>
              </div>
            </div>

            {/* Recording panel */}
            <div className="p-4 border-b border-border/50">
              <div className="flex items-center gap-3 p-3 rounded-xl bg-secondary/50 border border-border/30">
                {isRecording ? (
                  <>
                    <button
                      onClick={stopRecording}
                      className="w-10 h-10 rounded-xl bg-destructive/10 flex items-center justify-center text-destructive hover:bg-destructive/20 transition-colors recording-pulse"
                    >
                      <Square className="w-4 h-4 fill-current" />
                    </button>
                    <div className="flex-1">
                      <p className="text-sm font-medium">Recording...</p>
                      <p className="text-xs text-muted-foreground">
                        {eventCount + 1} events captured
                      </p>
                    </div>
                  </>
                ) : (
                  <>
                    <button
                      onClick={startRecording}
                      className="w-10 h-10 rounded-xl bg-primary/10 flex items-center justify-center text-primary hover:bg-primary/20 transition-colors"
                    >
                      <Circle className="w-4 h-4 fill-current" />
                    </button>
                    <div className="flex-1">
                      <p className="text-sm font-medium">Start Recording</p>
                      <p className="text-xs text-muted-foreground">
                        Capture your actions
                      </p>
                    </div>
                  </>
                )}
              </div>

              {/* Recording steps */}
              <AnimatePresence>
                {isRecording && (
                  <motion.div
                    initial={{ opacity: 0, height: 0 }}
                    animate={{ opacity: 1, height: "auto" }}
                    exit={{ opacity: 0, height: 0 }}
                    className="mt-3 space-y-1"
                  >
                    {recordingSteps.slice(0, currentStep + 1).map((step, i) => (
                      <motion.div
                        key={i}
                        initial={{ opacity: 0, x: -10 }}
                        animate={{ opacity: 1, x: 0 }}
                        className="flex items-center gap-2 px-3 py-1.5 rounded-lg bg-primary/5 text-xs"
                      >
                        <Check className="w-3 h-3 text-primary" />
                        <span className="text-muted-foreground">{step}</span>
                      </motion.div>
                    ))}
                  </motion.div>
                )}
              </AnimatePresence>
            </div>

            {/* Automations list */}
            <div className="p-4">
              <h4 className="text-xs font-semibold text-muted-foreground uppercase tracking-wider mb-3">
                Automations
              </h4>
              <div className="space-y-2">
                {mockAutomations.map((automation) => (
                  <motion.div
                    key={automation.id}
                    className={cn(
                      "group relative p-3 rounded-xl border bg-card/50 transition-all",
                      runningId === automation.id
                        ? "border-primary/30 shadow-sm"
                        : "border-border/30 hover:border-border/50 hover:bg-card/80"
                    )}
                    whileHover={{ scale: 1.01 }}
                  >
                    <div className="flex items-start gap-3">
                      <div
                        className={cn(
                          "w-8 h-8 rounded-lg flex items-center justify-center shrink-0",
                          runningId === automation.id
                            ? "bg-primary/10"
                            : "bg-muted/50"
                        )}
                      >
                        {runningId === automation.id ? (
                          <Loader2 className="w-4 h-4 text-primary animate-spin" />
                        ) : (
                          <Zap className="w-4 h-4 text-muted-foreground group-hover:text-primary transition-colors" />
                        )}
                      </div>
                      <div className="flex-1 min-w-0">
                        <h5 className="text-sm font-medium truncate">
                          {automation.name}
                        </h5>
                        <p className="text-xs text-muted-foreground">
                          {automation.events} events • {automation.duration} •{" "}
                          {automation.time}
                        </p>
                      </div>
                      <div className="flex items-center gap-1 opacity-0 group-hover:opacity-100 transition-opacity">
                        <button
                          onClick={() => shareAutomation(automation.id)}
                          className={cn(
                            "w-7 h-7 rounded-lg flex items-center justify-center transition-colors",
                            showShareSuccess === automation.id
                              ? "bg-success/10 text-success"
                              : "hover:bg-primary/10 hover:text-primary"
                          )}
                        >
                          {showShareSuccess === automation.id ? (
                            <Check className="w-3.5 h-3.5" />
                          ) : (
                            <Share2 className="w-3.5 h-3.5" />
                          )}
                        </button>
                        <button
                          onClick={() => runAutomation(automation.id)}
                          disabled={runningId === automation.id}
                          className="w-7 h-7 rounded-lg flex items-center justify-center hover:bg-primary/10 hover:text-primary transition-colors"
                        >
                          <Play className="w-3.5 h-3.5 fill-current" />
                        </button>
                        <button className="w-7 h-7 rounded-lg flex items-center justify-center hover:bg-secondary transition-colors">
                          <MoreHorizontal className="w-4 h-4" />
                        </button>
                      </div>
                    </div>

                    {/* Running progress bar */}
                    {runningId === automation.id && (
                      <motion.div
                        initial={{ scaleX: 0 }}
                        animate={{ scaleX: 1 }}
                        transition={{ duration: 3 }}
                        className="absolute bottom-0 left-0 right-0 h-0.5 bg-primary rounded-b-xl origin-left"
                      />
                    )}
                  </motion.div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
